// return the user data from the session storage
export const getUser = () => {
  const userStr = localStorage.getItem('UserName');
  if (userStr) return JSON.parse(userStr);
  else return null;
}
 
// return the token from the session storage
export const getToken = () => { 
  const itemStr = localStorage.getItem('Token') 
				// if the item doesn't exist, return null
				if (!itemStr) {
					return null
				}

        Configuration.USER_ID = JSON.parse(localStorage.getItem('UserId')).value;
        Configuration.USER_NAME = (JSON.parse(localStorage.getItem('UserName')).value).toUpperCase();
        Configuration.AUTH_TOKEN = JSON.parse(localStorage.getItem('Token')).value;
        Configuration.FACTORY = (JSON.parse(localStorage.getItem('Factory')).value).toUpperCase();

				const item = JSON.parse(itemStr)
				const now = new Date()

				// compare the expiry time of the item with the current time
				if (now.getTime() > item.expiry) {
					// If the item is expired, delete the item from storage
					// and return null
          removeUserSession();
					return null
				}
				return item.value

 // return localStorage.getItem('token') || null;
}
 
// remove the token and user from the session storage
export const removeUserSession = () => {
  localStorage.removeItem('Token');
  localStorage.removeItem('UserName');
  localStorage.removeItem('UserId');
}
 
// set the token and user from the session storage
export const setUserSession = (key,value, expiryTime) => {  
  const now = new Date()
  const SessionItem = {
    value: value,
    expiry: now.getTime() + expiryTime,
  }
  localStorage.setItem(key,  JSON.stringify(SessionItem)); 
}

